  methods: {
    getColor(status) {
      if (status == "Delayed") return "red";
      else if (status == "Gate Closed") return "red";
      else if (status == "Final Call") return "red";
      else if (status == "Departed") return "red";
      else if (status == "Check-in Open") return "blue";
      else if (status == "Boarding") return "blue";
      else if (status == "Security Check") return "blue";
      else if (status == "Arrived") return "success";
      else if (status == "On Time") return "success";
      else return "success";
    },
    rowClick(row) {
      // console.log("row ", moment(row["scheduleTime"]).format("MM/DD/YYYY"));
      row["scheduleTimeShow"] = moment(parseInt(row["scheduleTime"])).format(
        "DD/MM/YYYY"
      );
      console.log("rowClick", row);

      this.$store.commit("setFlightData", row);

      this.$router.push("trackflights/");
      console.log(row.name);
    },
    departureClick(row) {
      console.log("departureClick", row);
      this.$store.commit("setFlightData", row);
      this.$router.push("trackflightsdepature/");
    },
    getFlights() {
      //added by vijay
      try {
        //  let addy ="C2D21"
        //  const streetAddress = addy.substring(0, addy.indexOf(","));
        this.DisplayCurrentTime();

        let yesterday = new Date(
          new Date().getTime() - 4 * 60 * 60 * 1000
        ).getTime();
        let tillDateTime = new Date().getTime() + 24 * 60 * 60 * 1000;

        axios
          .get(
            process.env.VUE_APP_API_URL +
              `flightsroute/getflightfeed?appname=maa&starttime=${yesterday}&endtime=${tillDateTime}`
          )
          .then((flightRes) => {
            if (flightRes.data.success) {
              this.tableLoader = false;
              this.allflights = [];
              flightRes.data.flights.forEach((element) => {
                let scheduled = new Date(element.scheduledArrivalDate);
                let estimated = new Date(element.estimatedArrivalDate);
                element["img"] =
                  "http://development.wisefly.in/flightlogo/getimage/" +
                  element.airlineCode +
                  ".png";

                let minutes =
                  scheduled.getMinutes() < 10
                    ? "0" + scheduled.getMinutes()
                    : scheduled.getMinutes();

                element["scheduled"] = scheduled.getHours() + ":" + minutes; //scheduled.getMinutes();

                minutes =
                  estimated.getMinutes() < 10
                    ? "0" + estimated.getMinutes()
                    : estimated.getMinutes();

                console.log(
                  "minutes" +
                    minutes +
                    "  estimated.getMinutes " +
                    estimated.getMinutes()
                );
                element["estimated"] = estimated.getHours() + ":" + minutes; //estimated.getMinutes();
                element["estimatedTime"] = estimated.getHours() + ":" + minutes; //estimated.getMinutes();
                this.allflights.push(element);
              });
              // this.allflights = flightRes.data.flights;
              // console.log("this.allflights" , this.allflights)

              this.flightlist = this.allflights.filter((e) => {
                return e.nature === "DA" || e.nature == "IA";
              });
              this.filterlist = this.flightlist;
              console.log("761 ", this.flightlist);
              this.filterlist.forEach((e) => {
                // this.filterAllFlight.push({ text: e.originmap });
                this.allterminals.push({ text: "Terminal-" + e.terminal });
              });
              this.allterminals.push({ text: "Terminal-" + 4 });

              // this.allterminals.sort();
              this.allterminals.sort((a, b) => {
                return parseInt(a.text) - parseFloat(b.text);
              });

              let filterData = this.homeSearchInfo();

              if (filterData.selectFlightMode != undefined) {
                if (filterData.selectFlightMode == "Arrivals")
                  this.isArrival = true;

                this.$refs[filterData.selectFlightMode].$el.click();
                this.filterCityFlight(filterData.searchFlight);
              }
            }
          })
          .catch((err) => {
            console.log(err);
          });
      } catch (error) {
        console.log(error);
      }
    },
    departureFlight() {
      this.isArrival = false;
      console.log("departureFlight");
      // this.filterAllFlight = [];
      this.allterminals = [];
      this.resetFilter();
      //Departed
      console.log("departureFlight", this.allflights);
      this.departureslist = this.allflights.filter((e) => {
        return e.nature === "DD" || e.nature == "ID";
      });
      // this.isArrival = !this.isArrival;
      this.filterDeparturelist = this.departureslist;
      this.filterDeparturelist.forEach((e) => {
        // console.log(e.destinationmap)
        // this.filterAllFlight.push({ text: e.destinationmap });
        this.allterminals.push({ text: "Terminal-" + e.terminal });
      });
      this.allterminals.push({ text: "Terminal-" + 4 });
      this.allterminals.sort((a, b) => {
        return parseInt(a.text) - parseFloat(b.text);
      });
    },
    arrivalFlight() {
      console.log("arrivalFlight");
      this.resetFilter();
      // this.isArrival = !this.isArrival;
      // this.filterAllFlight = [];
      this.allterminals = [];
      this.filterlist.forEach((e) => {
        // this.filterAllFlight.push({ text: e.originmap });
        this.allterminals.push({ text: e.terminal });
      });
      this.allterminals.sort((a, b) => {
        return parseInt(a.text) - parseFloat(b.text);
      });
      // console.log(" this.filterAllFlight ", this.filterAllFlight);
    },
    filterCityFlight(e) {
      //-4 pluse 24
      console.log("filterCityFlight ", e);
      //   console.log("filterCityFlight ", e.target.value);
      let searchValue = e.target != undefined ? e.target.value : e;
      // console.log(this.isArrival, searchValue);
      if (this.isArrival) {
        console.log(this.flightlist);
        // this.flightlist = this.filterlist;
        this.flightlist = this.flightlist.filter((f) => {
          if (
            f.flightName.toLowerCase().indexOf(searchValue.toLowerCase()) !=
              -1 ||
            f.originmap.toLowerCase().indexOf(searchValue.toLowerCase()) != -1
          )
            return f;
        });
      } else {
        console.log("784 ", this.filterDeparturelist);
        // this.departureslist = this.filterDeparturelist;
        this.departureslist = this.departureslist.filter((f) => {
          if (
            f.flightName.toLowerCase().indexOf(searchValue.toLowerCase()) !=
              -1 ||
            f.destinationmap.toLowerCase().indexOf(searchValue.toLowerCase()) !=
              -1
          )
            return f;
        });
        console.log("this.departureslist 796", this.departureslist);
      }
    },
    selectFlight() {
      console.log("selectFlight", this.select);

      this.flightlist = this.allflights;
      if (this.isArrival) {
        console.log("Line 831", this.filterlist);
        this.flightlist = this.filterlist;
        this.flightlist = this.filterlist.filter((f) => {
          if (this.select == "Domestic") {
            if (f.nature == "DA") return f;
          } else if (this.select == "International") {
            if (f.nature === "IA") return f;
          }
          if (f.originmap == this.select) return f;
        });
        if (this.select == "All") this.flightlist = this.allflights;
        // if (this.flightlist.length) this.filterlist = this.flightlist;
      } else {
        this.departureslist = this.filterDeparturelist;
        this.departureslist = this.departureslist.filter((f) => {
          // if (f.destinationmap == this.select) return f;
          if (this.select == "Domestic") {
            if (f.nature == "DD") return f;
          } else if (this.select == "International") {
            if (f.nature === "ID") return f;
          }
        });
        if (this.select == "All") this.flightlist = this.allflights;
        // if (this.departureslist.length)
        //   this.filterDeparturelist = this.departureslist;
      }

      console.log("this.flightlist ", this.flightlist);
    },
    selectTerminal() {
      console.log("selectTerminal", this.selectedTerminal);
      let selectedTerminal = this.selectedTerminal.split("-")[1];

      if (this.isArrival) {
        console.log("849 ", this.flightlist);
        this.flightlist = this.filterlist;
        if (this.selectedTerminal !== "All Terminals")
          this.flightlist = this.flightlist.filter((f) => {
            if (f.terminal == selectedTerminal.toString()) return f;
          });
        if (this.flightlist.length) this.filterlist = this.flightlist;
      } else {
        this.departureslist = this.filterDeparturelist;
        if (this.selectedTerminal !== "All Terminals")
          this.departureslist = this.departureslist.filter((f) => {
            if (f.terminal == selectedTerminal.toString()) return f;
          });
        if (this.departureslist.length)
          this.filterDeparturelist = this.departureslist;
      }
      console.log("selectTerminal 955 ", this.selectedTerminal);
    },
    homeSearchInfo() {
      console.log(
        "Methods  this.homeSearchInfo() ",
        this.$store.getters.homeSearch
      );
      return this.$store.getters.getHomeFligthSearch;
    },
    resetFilter() {
      this.flightlist = this.allflights;
      this.departureslist = this.filterDeparturelist;
      this.selectedTerminal = "";
      this.select = "";
      this.searchFlightCity = "";
    },
    DisplayCurrentTime() {
      let date = new Date();
      console.log(
        date.getDate() + ":" + date.getMonth() + ":" + date.getFullYear()
      );

      let hours = date.getHours() > 12 ? date.getHours() - 12 : date.getHours();

      console.log("date.getHours()" + date.getHours());
      let am_pm = date.getHours() >= 12 ? "PM" : "AM";
      hours = hours < 10 ? "0" + hours : hours;
      let minutes =
        date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
      let month =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      console.log("month " + month);
      this.istTime =
        date.getDate() +
        ":" +
        month +
        ":" +
        date.getFullYear() +
        " " +
        date.getHours() +
        ":" +
        minutes;

      console.log("this.istTime" + this.istTime);
    },
  },
  mounted() {
    console.log(apiurl);
    // console.log( this.$store.state)
    this.getFlights();
    this.DisplayCurrentTime();

    // if (filterData) {
    //     console.log("Line 830 ")
